.select-box--arrow {
  width: 30px;
  height: 30px;
  margin: 0;
  padding: 0;
  display: inline-block;
  background: #535d63;
  position: absolute;
  right: 0;
  top: 0;
  cursor: pointer;
}

.select-box--arrow-down {
  position: relative;
  top: 22px;
  left: 8px;
  width: 0;
  height: 0;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-top: 10px solid #fff;
}

.select-box--arrow-up {
  position: relative;
  bottom: 18px;
  left: 8px;
  width: 0;
  height: 0;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-bottom: 10px solid #fff;
}

.select-box--container {
  height: 42px;
  border: 1px solid #535d63;
  border-radius: 3px;
  width: 100%;
  margin: 0;
  cursor: pointer;
  font-size: 20px;
}

.selected-box--selected-item {
  display: inline-block;
  height: 100%;
  width: 100%;
  vertical-align: middle;
}

.select-box--items div {
  border-bottom: 1px solid #535d63;
  border-left: 1px solid #535d63;
  border-right: 1px solid #535d63;
  padding: 6px;
  padding-left: 20px;
  background: white;
}

.select-box--items div:hover {
  background: #535d63;
  color: white;
}
.select-box--box {
  position: relative;
}

.items-container {
  margin-top: 20.5px;
  cursor: pointer;
}

.link {
    text-decoration: none;
    color: #535d63;
    z-index: 2;
    font-weight: bold;
    border-radius: 5px;
    text-align: center;
    padding: 0 1em;
    font-size: 1.25em;
}

.link:hover {
    color: white;
    background: #8d9397;
    -webkit-transition: font-size .1s ease-in;
    transition: font-size .1s ease-in;
    cursor: pointer;
}

.link:hover a {
    color: white;
}

.link:hover li {
    color: white;
}
.droplinks {
